import React, {
  useState,
  useEffect,
  useRef,
  useCallback,
  useContext,
} from "react";
import { Button, Tab, Tabs } from "react-bootstrap";
import globalicon from "../../assets/images/chat/global.svg";
import groupicon from "../../assets/images/chat/group.svg";
// import closeicon from "../../assets/images/chat/close-Icon.svg";
// import messageicon from "../../assets/images/chat/chat.svg";
import "./chat.css";
import GroupChat from "./groupChat";
import toast from "react-hot-toast";
import CreateGroup from "./createGroup";
import GlobalChat from "./globalChat";
import { getAllUsers, CreateGroupApi, getUserChatsApi } from "../../utils/Api";
import socket from "../../utils/socket";
import UserContext from "../../context/UserContext";
import { getToken } from "../../config/axios";
import { ChatsCloseIconSVG } from "../SVG/IconSVG";

const Chat = ({chatmenu, setChatMenu}) => {
  const { user } = useContext(UserContext);
  const messagesEndRef = useRef(null);
  const [messages, setMessages] = useState([]);

  const [key, setKey] = useState("global");
  const [createGroup, setCreateGroup] = useState(false);

  const handleGroup = () => {
    setCreateGroup(!createGroup);
  };
  //creating group
  const [groupChatName, setGroupChatName] = useState("");
  const [selectedUsers, setSelectedUsers] = useState([]);
  const [options, setOptions] = useState([]);
  const [users, setUser] = useState([]);
  const [groupChats, setGroupChats] = useState([]);
  const [groupChatId,setGroupChatId] = useState('');


  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messagesEndRef]);
  // api calls function start----
  const getUsers = useCallback(async () => {
    if (getToken()) {
      const res = await getAllUsers(setUser);
      let arr = [];
      res.results &&
        res.results.length > 0 &&
        res.results.forEach((el) => {
          if (user.id !== el.id) {
            let obj = {};
            obj.label = el?.name;
            obj.value = el?.id;
            arr.push(obj);
          }
        });
      setOptions(arr);
    }
  }, [user?.id]);
  const getChats = async () => {
    if(getToken()){
      const res = await getUserChatsApi();
      if (res.status === 201) {
        setGroupChats(res.data);
      }
    }
  };

  // End------
  const createGroupFn = async () => {
    if (selectedUsers.length > 2 || groupChatName !== "") {
      const userId = selectedUsers.map((el) => el.value);
      const res = await CreateGroupApi({ users: userId, name: groupChatName });
      if (res.status === 201 && res.statusText === "Created") {
        getChats();
        setCreateGroup(false);
        setGroupChatName("");
        setSelectedUsers([]);
        toast.success("Group created");
      } else {
        toast.error(res.data.message);
      }
    }
  };
  useEffect(() => {
    getUsers();
    getChats();
  }, [getUsers]);

  useEffect(() => {
    socket.on("message-recieved", (newMessageRecieved) => {
      let copyData= [...messages];
      copyData.push(newMessageRecieved);
      const newData = copyData.filter((el)=> el?.chat?._id ===groupChatId);
      setMessages(newData);
      scrollToBottom();
    });
  }, [scrollToBottom, messages,groupChatId]);

  useEffect(() => {
    if (messagesEndRef) {
      scrollToBottom();
    }
  }, [messagesEndRef, messages, scrollToBottom]);

  return (
    <div className='chat'>
      <div className='chat-view'>
        <div className='all-bets'>
        <Button className="close-chats" onClick={()=>setChatMenu(!chatmenu)}> <ChatsCloseIconSVG /></Button>
          <Tabs
            id='controlled-tab-example'
            activeKey={key}
            onSelect={(k) => setKey(k)}>
            <Tab eventKey='global' title={<img src={globalicon} alt='' />}>
              <GlobalChat />
            </Tab>
            <Tab eventKey='group' title={<img src={groupicon} alt='' />}>
              <GroupChat
                handleGroup={handleGroup}
                groupChats={groupChats}
                messages={messages}
                setMessages={setMessages}
                scrollToBottom={scrollToBottom}
                messagesEndRef={messagesEndRef}
                groupChatId={groupChatId}
                setGroupChatId={setGroupChatId}
              />
            </Tab>
          </Tabs>
          
        </div>

        {/* <h2>192 Online <span onClick={()=>setChatMenu(!chatmenu)} className={`${chatmenu ? `` : `hide-chat-btn`} `} role="presentation">{chatmenu ? <img src={closeicon} alt="" />: <img src={messageicon} alt="" />}</span></h2>
         */}
      </div>
      <CreateGroup
        show={createGroup}
        handleModal={handleGroup}
        allUsers={users}
        options={options}
        setGroupChatName={setGroupChatName}
        setSelectedOption={setSelectedUsers}
        selectedOption={selectedUsers}
        groupChatName={groupChatName}
        createGroupFn={createGroupFn}
      />
    </div>
  );
};

export default Chat;
