import React, { useState, useEffect, useRef } from "react";
import Layout from "../layout/layout";
import { Tab, Nav, Dropdown } from "react-bootstrap";
import Withdraw from "./Withdraw";
import Deposit from "./Deposit";
import "./style.css";
import { paymentInstance } from "../../config/axios";
import axios from "axios";

const Index = () => {
  const [key, setKey] = useState("deposit");
  const inputRef = useRef(null);
  const [depositData, setDepositData] = useState({ address:"", qrCode:"", expDate:"",message:"", loader:true });

  useEffect(() => {
    (async () => {
      try {
        const data = await paymentInstance().post('/deposit');
        setDepositData({...data.data,loader:false })
        console.log({data})
      } catch (error) {
         if(axios.isAxiosError(error) && error.response){
          setDepositData({address:"", qrCode:"", expDate:"",message: error.response.data.message, loader:false })
         }
      }

    })()

  }, []);

  console.log({depositData})


  useEffect(() => {
    if (inputRef) {
      inputRef.current?.focus();
    }
  }, [inputRef, key]);
  const handleDeopsitSubmit = async() =>{
    const data = await paymentInstance().post('/deposit');
    setDepositData({...data.data,loader:false })
    console.log("workinngdfklajsdfkjlkaj")
  }
  return (
    <Layout>
      <div className="container">
        <div className="widthdraw-deposit-page">
          <div className="profile-grid">
            <Tab.Container id="left-tabs-example" 
            // defaultActiveKey="withdraw"
            activeKey={key}
            onSelect={(k) => setKey(k)}
            >
              <div className="profile-sidebar">
                <Dropdown className="profile-mobileMenu" id="dropdown-basic">
                  <Dropdown.Toggle>
                    {key === 'withdraw' ? 'Withdraw' : key === 'deposit' ? 'Deposit' : 'Wallet'}
                  </Dropdown.Toggle>

                  <Dropdown.Menu>
                    <Dropdown.Item eventKey="withdraw">Withdraw</Dropdown.Item>
                    <Dropdown.Item eventKey="deposit">Deposit</Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
                <div className="profile-menu">
                  <Nav variant="pills" className="flex-column">
                    <Nav.Item>
                      <Nav.Link eventKey="withdraw">Withdraw</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="deposit">Deposit</Nav.Link>
                    </Nav.Item>
                  </Nav>
                </div>
              </div>
           

              <div className="profile-content">
                <Tab.Content>
                  <Tab.Pane eventKey="deposit">
                    <div className="page-heading">
                      <h3>Deposit</h3>
                    </div>
                    <Deposit
                      depositData={depositData}
                       handleDeposit={handleDeopsitSubmit}
                    />
                  </Tab.Pane>
                  <Tab.Pane eventKey="withdraw">
                    <div className="page-heading">
                      <h3>Withdraw</h3>
                    </div>
                    <Withdraw inputRef={inputRef} />
                  </Tab.Pane>
                </Tab.Content>
              </div>
            </Tab.Container>
          </div>
          {/* <Tabs
            id="controlled-tab-example"
            activeKey={key}
            onSelect={(k) => setKey(k)}
          >
            <Tab eventKey="deposit" title="Deposit">
              <Deposit
                step={step}
                setStep={setStep}
                amount={amount}
                setAmount={setAmount}
                address={address}
                setAddress={setAddress}
                inputRef={depositInputRef}
              />
            </Tab>
            <Tab eventKey="withdraw" title="Withdraw">
              <Withdraw inputRef={inputRef} />
            </Tab>
          </Tabs> */}
        </div>
      </div>
    </Layout>
  );
};

export default Index;
