import axios from 'axios';
import { server } from '../config/keys';

export const getToken = () => {
  const tokenData = localStorage.getItem('token')
    ? JSON.parse(localStorage.getItem('token'))
    : {};
  const { access: { token } = {} } = tokenData;
  return token;
};

export const getAuthorizationHeader = () => `Bearer ${getToken()}`;

export const userInstance = () =>
  axios.create({
    baseURL: `${server}/v1/users`,
    headers: { Authorization: getAuthorizationHeader() },
  });

export const crashInstance = () =>
  axios.create({
    baseURL: `${server}/v1/crash`,
    headers: { Authorization: getAuthorizationHeader() },
  });

export const authInstance = () =>
  axios.create({
    baseURL: `${server}/v1/auth`,
    headers: { Authorization: getAuthorizationHeader() },
  });

export const chatInstance = () =>
  axios.create({
    baseURL: `${server}/v1/chat`,
    headers: {
      Authorization: getAuthorizationHeader(),
    },
  });

export const messageInstance = () =>
  axios.create({
    baseURL: `${server}/v1/message`,
    headers: {
      Authorization: getAuthorizationHeader(),
    },
  });

export const paymentInstance = () =>
  axios.create({
    baseURL: `${server}/v1/payment`,
    headers: {
      Authorization: getAuthorizationHeader(),
    },
  });
