import { Link } from "react-router-dom";
import logo from "../../assets/images/header/logo-v2.png";
import "./footer.css";

const Footer = () => {
  return (
    <div className="footer">
      <div className="container">
        <div className="footer-grid">
          <div className="footer-row">
            <div className="logo">
              <img src={logo} alt="" />
              <ul className="social-links">
                <li>
                  <Link to="#">
                    <i class="lab la-facebook-f"></i>
                  </Link>
                </li>
                <li>
                  <Link to="#">
                    <i class="lab la-instagram"></i>
                  </Link>
                </li>
                <li>
                  <Link to="#">
                    <i class="lab la-twitter"></i>
                  </Link>
                </li>
              </ul>
            </div>
            {/* <div className="footer-col"> */}
            <div className="footer-links">
              <h5>ABOUT</h5>
              <Link to="#">About</Link>
              <Link to="/">Crash</Link>
              <Link to="#">Contact</Link>
            </div>
            <div className="footer-links">
              <h5>SUPPORT</h5>
              <Link to="/privacy">Privacy</Link>
              <Link to="/terms">Terms &amp; Conditions</Link>
              <Link to="/faq">FAQ</Link>
            </div>
          </div>
          {/* </div> */}

          <p>
            © Copyright 2022 <span>To The Moon</span> | All Right Received.
          </p>
          <ul className="footer-end">
            <li><Link to="mailto:support@crash.com">support@crash.com</Link></li>
            <li><Link to="mailto:info@crash.com">info@crash.com</Link></li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Footer;
