import React from 'react';
import { Form, Button } from 'react-bootstrap';
import bitcoin from '../../assets/images/all/bitcoin.png';
import arrow from '../../assets/images/all/next.png';

const Withdraw = ({ inputRef }) => {
  return (
    <div className='user-withdraw-page'>
      <div className='withdraw-balance'>
        <div className='withdraw-amount'>
        <div className='amountSection'>
          <h4>Enter Amount</h4>
          <input ref={inputRef} type='Number' placeholder='0' name='amount' />
          </div>
          {/* <p>
            {errors && errors.amount && (
            <span className='text-danger'>{errors.amount}</span>
          )}
          </p> */}
        </div>
        {/* <p></p> */}
      </div>
      <div className='withdraw-details'>
        <div className='withdraw-list'>
          <div className='d-flex justify-content-between mb-1'>
            <span>To</span>
            <span>$50</span>
          </div>

          <div className='withdraw-field'>
            <Form.Control
              type='text'
              // value={values.address}
              name='address'
              placeholder='Enter address'
              // onChange={(e) => handleInputChange(e)}
              // onBlur={handleOnblur}
              // onKeyUp={handleKeyUp}
            />
            {/* {errors && errors.address && (
            <span className='text-danger'>{errors.address}</span>
          )} */}
          </div>
        </div>

        <div className='withdraw-list pay-with'>
          <p> Pay with</p>
          <div
            className='withdraw-field select-blockchain'
            // onClick={handleOpen}
          >
            <div className=''>
              <img src={bitcoin} alt='arrow' /> BTC
            </div>
            <img src={arrow} alt='arrow' />
          </div>
        </div>
      </div>
      <div className='deposit-btn'>
        <Button
          className='save-btn'
          type='submit'
          // onClick={() => handleSubmit()}
        >
          Continue
        </Button>
      </div>
      <div className='bitcoin-balance'>
        <p>BTC Balance : 0000.003</p>
        <p>BTC : $50</p>
      </div>
    </div>
  );
};

export default Withdraw;
