import React, {useState} from "react";
import { Button, Form, Spinner } from 'react-bootstrap';
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { useNavigate } from "react-router-dom";
import { ResetPasswordSchema } from "../../validationSchema/authSchema";
import { resetPassword } from "../../utils/Api";
import Layout from "../layout/layout";
import toast from 'react-hot-toast';


const ResetPassword = () => {
    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(false);
  const {
    handleSubmit,
    register,
    formState: { errors },
  } = useForm({ resolver: yupResolver(ResetPasswordSchema) });

  const handleResetPassword = async (values) => {
    setIsLoading(true);
    const urlParams = new URLSearchParams(window.location.search);
    const res = await resetPassword(values, urlParams.get("token"));
    setIsLoading(false);
    if (res.code === 200) {
      toast.success('Password update successfully.')
      setTimeout(() => {
        navigate("/");
      }, 1000);
    } else {
     toast.error(res.message)
    }
  };


    return (
        <Layout>
            <div className="verify-email-section">
                <div className="forget-section">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="forget-form">
                                    <h1>Reset <span>Password</span></h1>
                                    <p>Enter your new password</p>
                                    <div className="forget-box">
                                        <Form onSubmit={handleSubmit(handleResetPassword)}>
                                            <Form.Group className="form-group">
                                                <Form.Label>Password</Form.Label>
                                                <Form.Control type="password" placeholder="Email Password" 
                                                name="password"
                                                className={errors.password ? "error-field" : ""}
                                                {...register("password")}  /> 
                                                 {errors?.password ? (
                                                    <p className="error-text">{errors.password.message}</p>
                                                ) : (
                                                    ""
                                                )}                                               
                                            </Form.Group>
                                            <Form.Group className="form-group">
                                                <Form.Label>Confirm Password</Form.Label>
                                                <Form.Control type="password" placeholder="Confirm Password"  
                                                name="confirmPassword"
                                                className={errors.confirmPassword ? "error-field" : ""}
                                                {...register("confirmPassword")}/>
                                                {errors?.confirmPassword ? (
                                                    <p className="error-text">
                                                    {errors.confirmPassword.message}
                                                    </p>
                                                ) : (
                                                    ""
                                                )}                                                
                                            </Form.Group>
                                            <div className="login-button">
                                            <Button type="submit" className="l-btn">
                                            {isLoading ? (
                                                    <Spinner animation="border" />
                                                ) : (
                                                    "Reset Password"
                                                )}
                                            </Button>
                                           </div>
                                        </Form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    );
};

export default ResetPassword;