import React,{useState,useEffect }from 'react';
import bitcoin from '../../assets/images/all/bitcoin.png';
import arrow from '../../assets/images/all/next.png';
import { Button} from 'react-bootstrap';
import CopyToClipboard from 'react-copy-to-clipboard';

const Deposit = ({ depositData ,handleDeposit}) => {

  const [time, setTime] = useState();
  const [minutes ,setTimeAddress] = useState();


 
  useEffect(() => {
    let timer = setInterval(() => {
      const curiTime = Date.parse(depositData?.expDate) - Date.parse(new Date());
      const seconds = Math.floor( (curiTime/1000) % 60 ) <= 9 ? `0${Math.floor( (curiTime/1000) % 60 )}` : Math.floor( (curiTime/1000) % 60 );
      const minutes = Math.floor( (curiTime/1000/60) % 60 );
      const finalTime = minutes + ":" + seconds;
      setTimeAddress(minutes)
      // const curiTime = new Date(depositData?.expDate) - new Date();
      if(curiTime <= 0 ){
        clearInterval(timer);
        setTime(finalTime)
        return
      }
      setTime(finalTime);
    }, 1000);
    return () => {
      if(timer){
        clearInterval(timer);
      }
    }
  }, [depositData?.expDate]);

 




  return (
    <>
   
     {minutes >0 ? (<>
      <div className='user-deposit'>
      <img
        src={arrow}
        alt='arrow'
        // onClick={() => setStep(1)}
        className='back-arrow'
      />
      <div className='user-deposit-box'>
        <div className='bar-code-secion'>
          <img
            src={depositData?.qrCode}
            alt=''
          />
        </div>
        <div>
          <div className='assets-section select-blockchain'>
            <div className='asstes-left'>
              <img src={bitcoin} alt='' />
              <div className='bitxoin-heading '>
                <h6>BTC</h6>
              </div>
            </div>
          </div>
          <div className='assets-section btc-address-section'>
            <div className='asstes-left'>
              <div className='bitxoin-heading'>
                <h6>BTC Address</h6>
                <p>{depositData?.address}</p>
              </div>
            </div>
            
            <CopyToClipboard text={depositData?.address}>
             
               <i class="las la-clipboard" style={{fontSize:'30px'}}></i>
            </CopyToClipboard>
            
          </div>
        </div>
      </div>
      <span>
        Address will expire after {time ? time : 0} minutes
      </span>
      <div className='bitcoin-info'>
        <p>
          Deposit bitcoin in the above address. Only send BITCOIN to
          this address.We are not responsible if you send any other currency.
        </p>
      </div>
    </div>
     </>) :(<div className="qrCode-section">
      <Button onClick={handleDeposit}>
            Please regenerate QR Code
          </Button>
     </div>)}
  
  
    </>
  );
};

export default Deposit;
