import React, { useContext } from 'react';
import { NavLink } from 'react-router-dom';
import { Nav, Dropdown } from 'react-bootstrap';
import Layout from '../layout/layout';
import userimg from '../../assets/images/crash/bg.png';
// import logouticon from '../../assets/images/header/log-out-Regular.svg';
import UserContext from '../../context/UserContext';
import './profile.css';
import { LogOutIconSVG } from '../SVG/IconSVG';

const ProfileLayout = ({ children }) => {
  const { user } = useContext(UserContext);
  // const [mobSidebar, setMobSidebar] = useState(false);
  // const handleSidebar = () => {
  //   setMobSidebar(!mobSidebar);
  // };
  return (
    <Layout>
      <div className='profile-wrapper'>
        <div className='container'>
          <div className='profile-grid'>
            <div className='profile-sidebar'>
              <Dropdown className='profile-mobileMenu' id='dropdown-basic'>
                <Dropdown.Toggle>
                  Profile
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  <Dropdown.Item href='/profile'>Profile</Dropdown.Item>
                  <Dropdown.Item href='/betHistory'>Game History</Dropdown.Item>
                  <Dropdown.Item href='/transactionHistory'>Transaction History</Dropdown.Item>
                  <Dropdown.Item href='/settings'>Settings</Dropdown.Item>
                  <Dropdown.Item href='/Wallet'>Wallet</Dropdown.Item>
                  <Dropdown.Item href='/invitefriends'>
                    Invite Friends
                  </Dropdown.Item>
                </Dropdown.Menu>
              </Dropdown>
              <div className='profile-menu'>
                <div className='user-info'>
                  <div className='user-name'>
                    <img
                      src={user?.avatar ? user?.avatar : userimg}
                      alt='user'
                      onError={(e) => {
                        e.target.onerror = null;
                        e.target.src = userimg;
                      }}
                    />
                    <span>{user?.nickName}</span>{' '}
                  </div>

                  <div className='logout'>
                    <LogOutIconSVG />
                    {/* <img src={logouticon} alt='icon' /> */}
                    {/* Logout */}
                  </div>
                </div>

                <Nav>
                  <NavLink to='/profile'>
                    <i className='fa fa-user' aria-hidden='true'></i> Profile
                  </NavLink>
                  <NavLink to='/betHistory'>
                    <i className='fa fa-history' aria-hidden='true'></i>
                    Game History
                  </NavLink>
                  <NavLink to='/transactionHistory'>
                    <i className='fa fa-history' aria-hidden='true'></i>
                    Transaction History
                  </NavLink>
                  <NavLink to='/settings'>
                    <i className='fa fa-cog' aria-hidden='true'></i>
                    Settings
                  </NavLink>
                  {/* <NavLink to='/kyc'>
                    <i className='fa fa-cog' aria-hidden='true'></i>
                    KYC
                  </NavLink> */}
                  <NavLink to='/wallet'>
                    <i className='fa fa-cog' aria-hidden='true'></i>
                    Wallet
                  </NavLink>
                  <NavLink to='/invitefriends'>
                    <i className='fa fa-first-order' aria-hidden='true'></i>
                    Invite Friends
                  </NavLink>
                </Nav>
              </div>
            </div>  

            <div className='profile-content'>{children}</div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default ProfileLayout;
