import React, { useContext, useState } from "react";
import { Button, Form } from "react-bootstrap";
import userimg from "../../assets/images/crash/bg.png";
import ProfileLayout from "./profileLayout";
import UserContext from "../../context/UserContext";
import { uplaodImage, updateUser } from "../../utils/Api";
import toast from "react-hot-toast";
const Proflie = () => {
  const { user, setUser } = useContext(UserContext);

  const [userData, setUserData] = useState(user);
  const [errors, setErrors] = useState("");
  //   const { name, nickName, email } = user || {};
  //   const { name, username, email } = user || {};

  const handleOnChange = (e) => {
    const { name, value } = e.target;
    setUserData({ ...userData, [name]: value });
  };

  const handlePicUpload = async (e) => {
    const formData = new FormData();
    if (e.target.files.length > 0) {
      formData.append("avatar", e.target.files[0]);
      const response = await uplaodImage(formData);
      if (response.status === 200) {
        toast.success("Profile image changed");
        setUser(response.data.userData);
      } else {
        toast.error("Image has not been changed");
      }
    }
  };

  let checkEmail = /^[A-Za-z0-9.]{2,}@[A-Za-z0-9]{3,}[.]{1}[A-Za-z]{2,6}$/;
  let checkName = /^[A-Z a-z]{3,}$/;

  const handleOnBlure = async (e) => {
    const { name, value } = e.target;
    if (value === "") {
      setErrors({ ...errors, [name]: "This field is required" });
    } else if (name === "name" && !checkName.test(value)) {
      setErrors({ ...errors, [name]: "Enter only character" });
    } else if (name === "nickName" && !checkName.test(value)) {
      setErrors({ ...errors, [name]: `Enter only character` });
    } else if (name === "email" && !checkEmail.test(value)) {
      setErrors({ ...errors, [name]: `Email is not valid` });
    } else {
      setErrors({ ...errors, [name]: "" });
    }
  };

  const handleOnKeyUp = (e) => {
    const { name, value } = e.target;
    if (value !== "") {
      setErrors({ ...errors, [name]: "" });
    }
  };

  
	  const handleUpdateUser = async (e) => {
		e.preventDefault();	
    const response = await updateUser(userData);
    const { status, data } = response || {};

    if (status === 200) {
      toast.success("Profile Updated", { toastId: "update user" });
      setUser(data);
    }
  };

  return (
    <ProfileLayout>
      <div className="profile-page">
        <div className="page-heading">
          <h3>User Profile</h3>
          <Button type="submit">Edit</Button>
        </div>

        <div className="profile-page-content">
          <div className="user-image">
            {/* <img
							src={userimg}
							alt="user"
							onError={(e) => {
								e.target.onerror = null;
								e.target.src = userimg;
							}}
						/> */}
            <img
              src={user?.avatar ? user?.avatar : userimg}
              alt="user"
              onError={(e) => {
                e.target.onerror = null;
                e.target.src = userimg;
              }}
            />
            
            <div className="upload-profile">
			<input
              type="file"
              id="imageUpload"
              name="file"
              accept=".png, .jpg, .jpeg"
              onChange={handlePicUpload}
            />
								<label htmlFor="imageUpload">
								<i class="las la-camera"></i>
								</label>
							</div>

            {/* <div
								className="remove-profile"
							>
								<img
									src={remove}
									alt="user"
									onError={(e) => {
										e.target.onerror = null;
										e.target.src = userimg;
									}}
								/>
							</div> */}
          </div>
          <Form onSubmit={handleUpdateUser}>
            <div className="user-profile">
              <Form.Group
                className="mb-3 form-group"
                controlId="formBasicEmail"
              >
                <Form.Label>Name</Form.Label>
                <Form.Control
                  type="text"
                  name="name"
                  value={userData.name}
                  onChange={handleOnChange}
                  onBlur={handleOnBlure}
                  onKeyUp={handleOnKeyUp}
                />
                {errors && errors?.name && (
                  <span className="text-danger">{errors.name}</span>
                )}
              </Form.Group>
              <Form.Group
                className="mb-3 form-group"
                controlId="formBasicEmail"
              >
                <Form.Label>Nickname</Form.Label>
                <Form.Control
                  type="text"
                  name="nickName"
                  value={userData.nickName}
                  onChange={handleOnChange}
                  onBlur={handleOnBlure}
                  onKeyUp={handleOnKeyUp}
                />
                {errors && errors?.nickName && (
                  <span className="text-danger">{errors.nickName}</span>
                )}
              </Form.Group>
              <Form.Group
                className="mb-3 form-group"
                controlId="formBasicEmail"
              >
                <Form.Label>Email</Form.Label>
                <Form.Control
                  type="email"
                  name="email"
                  value={userData.email}
                  onChange={handleOnChange}
                  onBlur={handleOnBlure}
                  onKeyUp={handleOnKeyUp}
                />
              </Form.Group>
              {errors && errors?.email && (
                <span className="text-danger">{errors.email}</span>
              )}
            </div>
            <div className="edit-button">
              <Button type="submit">Save/ Update</Button>
            </div>
          </Form>
        </div>
      </div>
    </ProfileLayout>
  );
};

export default Proflie;
