// import crashicon from "../../../assets/images/crash/crash-icon.svg"
import { CrashIconSVG, BitcoinIconSVG, EthereumIconSVG } from "../../SVG/IconSVG";


const AllBets = () => {

    const [key, setKey] = useState('all');


    return (
        <div className="all-bets all-bets-table-section">
            

            <Tabs
                id="controlled-tab-example"
                activeKey={key}
                onSelect={(k) => setKey(k)}
                >
                <Tab eventKey="all" title="All Bets">
                    <BetList />
                </Tab>
                <Tab eventKey="mybets" title="My Bets">
                    <BetList />
                </Tab>
                <Tab eventKey="high" title="High Rollers">
                    <BetList />
                </Tab>
            </Tabs>

          
        </div>
    );
  }

export default AllBets;


