import React,{useEffect,useState} from "react";
import { Table } from "react-bootstrap";
import ReactPaginate from "react-paginate";
import ProfileLayout from "./profileLayout";
import btc from "../../assets/images/crash/bitcoin.svg"
// import eth from "../../assets/images/crash/eth.svg"
// import crashicon from "../../assets/images/crash/crash-icon.svg";
import {getAllTransactionHistory} from '../../utils/Api';
import {getTime} from  '../month/month';
import { useCallback } from "react";



const initialObj = {
  first: 1,
  last: "",
  next: 2,
  prev: 1,
  activeindex: 1,
  skip: 0,
  limit: 5,
};

const TransactionHistory = () => {

  const [tranasction,setTransactionHistory] = useState([]);
  const [paginationStep, setPaginationStep] = useState({ ...initialObj });
  
  const handlePagenation = (index) => {
   let  indexSelect = index.selected+1;
  let copyobj = { ...paginationStep };
  copyobj.activeindex = indexSelect;

  if (index.selected < paginationStep.last) {
    copyobj.next = index.selected + 1;
  }
  if (index.selected !== 1) {
    
    copyobj.prev = index.selected - 1;
  }
  copyobj.skip = paginationStep?.limit * (index.selected - 1);


  getHistory(copyobj);
  setPaginationStep(copyobj);
};

const  getHistory = useCallback(async (paginationObj, step) => {

  const result = await getAllTransactionHistory(setTransactionHistory, paginationObj);

  if (result && step === "first") {
    let copyObj = { ...paginationObj };
    
    copyObj.last = result?.totalPages;
    // copyObj.status = viewStatus;
    setPaginationStep(copyObj);
   
  }
},[]);




useEffect(() =>{
  getHistory(initialObj, "first")
},[getHistory])




  return (
    <ProfileLayout>
       {console.log("tranasction",tranasction)}
      <div className="bet-history">
        <div className="page-heading">
          <h3>
             Transaction History
          </h3>
        </div>
        <div className="bet-history-content">
        
         <Table bordered variant="dark" responsive>
                <thead>
                    <tr>
                    <th>PaymentMode</th>
                    <th>Time</th>
                    <th>Amount</th>
                
                    <th>Status</th>
                    </tr>
                </thead>
                <tbody>
                  {tranasction.results?.map((e,index) =>{
                  return(
                    <tr key={e.id}>
                 
                         <td>{e.paymentMode}</td>
                        <td>{getTime(e.createdAt)}</td>
                        <td><div className="bet-amount"><img src={btc} alt="" /> {e.amount}</div></td>
                   
                        <td>{e.status}</td>
                       

                    </tr>
                   )
                    })}

                 
           

                   
                </tbody>
            </Table>
          
          <div className="pagination">
            
              <ReactPaginate
                previousLabel=" Previous"
                marginPagesDisplayed={-2}
                pageRangeDisplayed={tranasction?.totalPages}
                pageCount={tranasction?.totalPages}
                onPageChange={handlePagenation}
                containerClassName="pagination-chk"
                previousLinkClassName="pagination__link"
                nextLinkClassName="pagination__link"
                disabledClassName="pagination__link--disabled"
                activeClassName="pagination__link--active"
                nextLabel="Next "
              />
          </div>
        </div>
      </div>
    </ProfileLayout>
  );
};

export default TransactionHistory;
