import React, { useState,useEffect } from "react";
import { Button, FormControl, Form } from "react-bootstrap";
// import user from "../../assets/images/chat/user.png";
// import user1 from "../../assets/images/chat/user1.png";
// import user2 from "../../assets/images/chat/user2.png";
// import emoji from "../../assets/images/chat/emoji.svg";
// import sendicon from "../../assets/images/chat/send.svg";
import messageicon from "../../assets/images/chat/chat.svg";
import back from "../../assets/images/chat/back.svg";

// import closeicon from "../../assets/images/chat/close.svg";
// import messageicon from "../../assets/images/chat/chat.svg";
import "./chat.css";
import socket from "../../utils/socket";
import toast from "react-hot-toast";
import { getChatMessages,sendMessageData} from "../../utils/Api";
import { getToken } from "../../config/axios";
import { SendIconSVG } from "../SVG/IconSVG";
const GroupChat = ({ handleGroup,groupChats,messages,setMessages,scrollToBottom,messagesEndRef,setGroupChatId,groupChatId}) => {
   
    const [groupChat, setGroupChat] = useState(true);
    const [newMessage, setNewMessage] = useState("");
    const [socketConnected, setSocketConnected] = useState(false);
    const [typing, setTyping] = useState(false);
    // const [activeGroupName, setActiveGroupName] = useState('')
    // const [istyping, setIsTyping] = useState(false);

  const handleGroupChat = async (chatId,chatName) => {
    if (!chatId) return;
    try {
      const res = await getChatMessages(chatId);
      setMessages(res.data);
      setGroupChat(false);
      setGroupChatId(chatId)
      // setActiveGroupName(chatName)
      socket.emit("join", chatId);
    } catch (error) {
      toast.error({
        title: "Error Occured!",
        description: "Failed to Load the Messages",
        status: "error",
        duration: 5000,
        isClosable: true,
        position: "bottom",
      });
    }
  };

  const sendMessage = async (event,chatId) => {
    event.preventDefault();
    if (newMessage) {
      socket.emit("stop typing", chatId);
      try {
        setNewMessage('')
        const res = await sendMessageData({
        content: newMessage,
        chatId: chatId,
        })
        if(res.data){
          socket.emit("newMessage", res.data);
          setMessages([...messages, res.data]);
          scrollToBottom();
        }
      } catch (error) {
        toast.error({
          title: "Error Occured!",
          description: "Failed to send the Message",
          status: "error",
          duration: 5000,
          isClosable: true,
          position: "bottom",
        });
      }
    }
  };
  const typingHandler = (e) => {
    setNewMessage(e.target.value);

    if (!socketConnected) return;

    if (!typing) {
      setTyping(true);
      socket.emit("typing", groupChatId);
    }
    let lastTypingTime = new Date().getTime();
    var timerLength = 3000;
    setTimeout(() => {
      var timeNow = new Date().getTime();
      var timeDiff = timeNow - lastTypingTime;
      if (timeDiff >= timerLength && typing) {
        socket.emit("stop typing", groupChatId);
        setTyping(false);
      }
    }, timerLength);
  };

  const onBack=(e)=>{
    e.preventDefault()
    setGroupChat(true)
    setGroupChatId('')
  }
  useEffect(() => {
    socket.on("connected", () => setSocketConnected(true));
    // socket.on("typing", () => setIsTyping(true));
    // socket.on("stop typing", () => setIsTyping(false));
  }, []);

  useEffect(()=>{
    scrollToBottom();
  },[messages,scrollToBottom])
    return (
      <>
        <div className="chat-content group-chat">
          {groupChat ? (
            <>
              <div className="group-chat-box">
                {groupChats.length>0 && groupChats.map((item) => {
                  return (
                    <div
                      className="group-chat-user"
                      key={item?._id}
                      onClick={()=>handleGroupChat(item?._id,item?.chatName)}
                    >
                      <div className="group-chat-pic">
                        <img src={messageicon} alt="" />
                      </div>
                      <div className="group-chat-user-info">
                        <h4>{item?.chatName}</h4>
                        {/* {item?.latestMessage && <> <span>{item?.latestMessage?.sender}:  {item?.latestMessage?.content}</span></>} */}
                        {item?.isGroupChat && <> <span>{item?.users.length} users</span></>}
                      </div>
                    </div>
                  );
                })}
              </div>
            </>
          ) : (
            <>
              <div className="chat-box">
                <span className="back-chat" onClick={(e)=>onBack(e)}>
                  <img src={back} alt="" /> Back
                {/* <span>
                   Group Name : {activeGroupName}
                </span> */}
                </span>
                
                {messages && messages.length>0 && messages.map(
                  (item) => {
                    return (
                      <div key={item._id}>
                        <div className="container1">
                          <div className="chat-user">
                            <div className="chat-user-info">
                              <h4>
                                
                                {item?.sender?.name}:{" "}
                                {/* <span className="nickname">@sreb@123</span> */}
                                <span>
                                  {item?.content}
                                </span>
                              </h4>
                            </div>
                          </div>
                        </div>
                      </div>
                    );
                  }
                )}
               <span ref={messagesEndRef} />

              </div>
  
              <div className="chat-footer">
                <Form>
                  <div className="chat-input">
                    <FormControl
                      placeholder="Your Message"
                      maxLength={160}
                      onChange={(e) => typingHandler(e)}
                      value={newMessage}
                    />
                    {/* <img src={emoji} alt="" /> */}
                  </div>
                  <Button type="submit" onClick={(e)=>sendMessage(e,groupChatId)}>
                    {/* <img src={sendicon} alt="" /> */}
                    <SendIconSVG />
                  </Button>
                </Form>
              </div>
            </>
          )}
          {groupChat && getToken() && (
            <div className="create-group-btn">
              <Button onClick={handleGroup}>Create Group</Button>
            </div>
          )}
        </div>
      </>
    );
  };

export default GroupChat;