import React from "react";
import { Modal, Form, Button } from "react-bootstrap";
import Select from "react-select";
 
  const SelectStyles = {
    control: (provided) => ({
      ...provided,
      minHeight: 40,
      backgroundColor: "var(--primary-600) ",
      border: "1px solid var(--primary-400) !important",
      borderRadius: "8px",
      color: "#90929a",
      boxShadow: "none !important",
      position: "realtive",
      padding: 0,
      margin: 0,
    }),
    option: (provided, state) => ({
      ...provided,
      color: state.isSelected ? "var(--text-light-300) !important" : "var(--text-light-400) !important",
      cursor: "pointer",
      backgroundColor: `transparent !important`,
      padding: 8,
      width: "100%",
    //   backgroundColor: "#31343c66 !important",
      poition: "absolute",
      margin: 0,
      '&:hover': {
        backgroundColor: `var(--primary-400) !important`,
        color: `var(--text-light-400) !important`,
      },
      '&:focus': {
        backgroundColor: `var(--primary-400) !important`,
        color: `var(--text-light-400) !important`,
      },
    }),
    indicatorsContainer: base => ({
      ...base,
      cursor: "pointer",
    }),
    menu: base => ({
      ...base,
      borderRadius: '5px',
      backgroundColor: "var(--primary-600) ",
      border: "1px solid var(--primary-400) !important",
    }),
  };

const CreateGroup = ({ show, handleModal,options,setGroupChatName,setSelectedOption,selectedOption,groupChatName,createGroupFn}) => {


  return (
    <Modal
      centered
      className="login-popup create-group-popup"
      show={show}
      onHide={() => handleModal(false)}
    >
      <Modal.Header closeButton>
        <Modal.Title></Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <div className="create-group-content">
          <Form.Group className="mb-3 form-group" controlId="exampleForm.ControlInput1">
            <Form.Label>Group Name</Form.Label>
            <Form.Control type="text" max='50' placeholder="Enter Group Name" value={groupChatName} onChange={(e)=>setGroupChatName(e.target.value)}/>
          </Form.Group>
          <div className="invite-people">
          <Form.Group className="mb-3 form-group" controlId="exampleForm.ControlInput1">
            <Form.Label>Add People</Form.Label>
            <Select
              styles={SelectStyles}
              defaultValue={selectedOption}
              onChange={setSelectedOption}
              options={options}
              isMulti={true}
              // menuIsOpen
              classNamePrefix="react-select"
            />
             </Form.Group>
          </div>
          <div className="submit-btn">
            <Button onClick={createGroupFn}>Create Group</Button>
          </div>
        </div>
      </Modal.Body>
    </Modal>
  );
};
export default CreateGroup;
