import React, { useContext } from 'react';
import crashContext from '../../../context/crashContext';
import btc from '../../../assets/images/crash/bitcoin.svg';
import { Table } from 'react-bootstrap';

const JoinUser = () => {
  const { gameState } = useContext(crashContext);
  return (
    <>
      <div className='bet-total-amount'>
        <span>{gameState?.joinedUsers?.length || 0} Player</span>
        <span>
          $
          {Array.isArray(gameState?.joinedUsers)
            ? gameState.joinedUsers
                .reduce((prev, cur) => prev + cur.amount, 0)
                .toFixed(2)
            : 0.0}
        </span>
      </div>
      <div className='bet-list'>
        <Table variant='dark' responsive>
          <tbody>
            {Array.isArray(gameState.joinedUsers) &&
              gameState.joinedUsers.map((el) => (
                <tr key={el.userid._id}>
                  <td>{el.userid.nickName}</td>
                  <td>{!el?.isCashedOut ? '---' : '1.02x'}</td>
                  <td>
                    <div className='payout green-text'>
                      <img src={btc} alt='' /> {el.amount.toFixed(2)}
                    </div>
                  </td>
                </tr>
              ))}
          </tbody>
        </Table>
      </div>
    </>
  );
};

export default JoinUser;
