export const getAllTransactionHistory= async (setTransactionHistory,payload) => {
  try {
 
    const res = await paymentInstance().get(`/getdeposit?limit=${payload.limit}&page=${payload.activeindex}`);

    setTransactionHistory(res.data.transactionList)
    return res.data;
  } catch (error) {
    console.log('Error in updateUser api =>', error);
    return error.response.data;
  }
};


