import React, {useState} from "react";
import { Button, Form, Spinner } from 'react-bootstrap';
import { useNavigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { ForgetPasswordSchema } from "../../validationSchema/authSchema";
import { forgetPassword } from "../../utils/Api";
import toast from 'react-hot-toast';
import "./forget.css";

const Forget = ({handleModal}) => {
    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(false);
    const {
      handleSubmit,
      register,
      formState: { errors },
    } = useForm({ resolver: yupResolver(ForgetPasswordSchema) });
  
    const handleForget = async (values) => {
      setIsLoading(true);
      const res = await forgetPassword(values);
      setIsLoading(false);
      if (res.code === 200) {
        handleModal(false);
        setTimeout(() => {
            navigate(`/reset-password?token=${res.token}`);
          }, 1000);
      } else {
        console.log('res.message--',res.message);
        toast.error(res.message)
      }
    };
    return (
            <div className="forget-password-section">
                <div className="forget-section">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="forget-form">
                                    <h1>Forget <span>Password?</span></h1>
                                    <p>Enter your email to reset your password</p>
                                    <div className="forget-box">
                                        <Form onSubmit={handleSubmit(handleForget)}>
                                            <Form.Group className="form-group">
                                                <Form.Label>Email</Form.Label>
                                                <Form.Control type="email" placeholder="Enter your email" 
                                                name="text"
                                                className={errors.email ? "error-field" : ""}
                                                {...register("email")}  />  
                                                 {errors?.email ? (
                                                        <p className="error-text">{errors.email.message}</p>
                                                    ) : (
                                                        ""
                                                    )}                                              
                                            </Form.Group>
                                            <div className="login-button">
                                            <Button className="l-btn" type="submit">
                                            {isLoading ? (
                                                <Spinner animation="border" />
                                            ) : (
                                                "Forgot Password"
                                            )}
                                            </Button>
                                        </div>
                                        </Form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
    );
};

export default Forget;