import React, {useState} from "react";
import { Form, Button, Spinner } from "react-bootstrap";
import { useForm } from "react-hook-form";
// import { useNavigate } from "react-router-dom";
import { yupResolver } from "@hookform/resolvers/yup";
import { RegisterSchema } from "../../validationSchema/authSchema";
import { signup } from "../../utils/Api";
import PhoneInput from "react-phone-input-2";
// import toast from 'react-hot-toast';
import "./register.css";

const Register = ({ handleModal }) => {
  const [isLoading, setIsLoading] = useState(false);
//   const [showPasswordEye, setShowPasswordEye] = useState([]);
  const {
    handleSubmit,
    register,
    setValue,
    formState: { errors },
    setError,
  } = useForm({ resolver: yupResolver(RegisterSchema) });

  const handleSignup = async (values) => {
    setIsLoading(true);
    const res = await signup(values);
    setIsLoading(false);
    if (res.message === "Email already taken") {
      setError(
        "email",
        { type: "custom", message: "Email already taken" },
        { shouldFocus: true }
      );
    } else if (res.msg === "Email send for verification") {
      handleModal("verifying");
    }
  };

//   const showPassword = (name) => {
//     if (!showPasswordEye.includes(name)) {
//       setShowPasswordEye((old) => [...old, name]);
//     } else {
//       setShowPasswordEye((old) => old.filter((el) => el !== name));
//     }
//   };

  const handleChange = (e) => {
    setValue("phoneNumber", e);
    setError("phoneNumber", "customError");
  };

    return (
        <div className="signup">
            <div className="register-section">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="register-form">
                                <h1>Sign In</h1>
                                <p>Welcome, Please fill your detail to create your account</p>
                              
                                <div className="rigister-tab">
                                    <Form onSubmit={handleSubmit(handleSignup)}>
                                    <div className="half-width">
                                        <Form.Group className="form-group">
                                            <Form.Label>Name</Form.Label>
                                            <Form.Control
                                                type="text"
                                                name="name"
                                                placeholder="Enter your name"
                                                className={errors.name ? "error-field" : ""}
                                                {...register("name")}
                                                autoComplete="off"
                                            />
                                             {errors?.name ? (
                                                <p className="error-text">{errors.name.message}</p>
                                                ) : (
                                                ""
                                             )}
                                        </Form.Group>
                                        <Form.Group className="form-group">
                                            <Form.Label>Nickname</Form.Label>
                                            <Form.Control
                                                type="text"
                                                placeholder="Enter your nickname"
                                                name="nickName"
                                                className={errors.nickName ? "error-field" : ""}
                                                {...register("nickName")}
                                                autoComplete="off"
                                            />
                                            {errors?.nickName ? (
                                                <p className="error-text">{errors.nickName.message}</p>
                                                ) : (
                                                ""
                                                )}
                                        </Form.Group>
                                        </div>

                                        <div className="half-width">
                                       <Form.Group className="form-group">
                                            <Form.Label>Email</Form.Label>
                                            <Form.Control
                                                type="text"
                                                placeholder="Enter your email"
                                                name="email"
                                                className={errors.email ? "error-field" : ""}
                                                {...register("email")}
                                                autoComplete="off"
                                            />
                                              {errors?.email ? (
                                                <p className="error-text">{errors.email.message}</p>
                                                ) : (
                                                ""
                                                )}
                                        </Form.Group>
                                        <Form.Group className="form-group">
                                            <Form.Label>Phone number</Form.Label>
                                            <PhoneInput
                                            country={"us"}
                                            onChange={handleChange}
                                            inputProps={{
                                                name: "phoneNumber",
                                            }}
                                            className={errors.phoneNumber ? "error-field" : ""}
                                            />

                                            {errors?.phoneNumber ? (
                                            <p className="error-text">{errors.phoneNumber.message}</p>
                                            ) : (
                                            ""
                                            )}
                                        </Form.Group>
                                        </div>
                                        <Form.Group className="form-group">
                                            <Form.Label>Password</Form.Label>
                                            <Form.Control
                                                type="password"
                                                placeholder="Enter your password"
                                                className={errors.password ? "error-field" : ""}
                                                name="password"
                                                {...register("password")}
                                                autoComplete="off"

                                            />
                                            {errors?.password ? (
                                            <p className="error-text">{errors.password.message}</p>
                                            ) : (
                                            ""
                                            )}
                                        </Form.Group>
                                        <Form.Group className="form-group">
                                            <Form.Label>Confirm Password</Form.Label>
                                            <Form.Control
                                                type="password"
                                                placeholder="Confirm password"
                                                name="verifyPassword"
                                                className={errors.verifyPassword ? "error-field" : ""}
                                                {...register("verifyPassword")}
                                                autoComplete="off"

                                            />
                                              {errors?.verifyPassword ? (
                                            <p className="error-text">
                                                {errors.verifyPassword.message}
                                            </p>
                                            ) : (
                                            ""
                                            )}
                                        </Form.Group>
    
                                        <div className="text-center">
                                            <Button className="s-btn" type="submit">
                                                {isLoading ? <Spinner animation="border" /> : "Create Account"} 
                                            </Button>
                                        </div>

                                        {/* <div className="social-login">
                                            <span>OR</span>
                                            <div className="social-btns">
                                                <Button className="facebook-btn"><i className="fa fa-facebook" /> Login with Facebook</Button>
                                                <Button className="twitch-btn"><i className="fa fa-steam" /> Login with Steam</Button>
                                            </div>
                                        </div> */}

                                    </Form>
                                    <p className="already">Already have an account? <Button className="forget-link" onClick={()=>handleModal("login")}>
                                        Login</Button>
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Register;
