import { useState, useContext } from 'react';
import {
  Tab,
  Tabs,
  Form,
  InputGroup,
  Button,
  Tooltip,
  OverlayTrigger,
} from 'react-bootstrap';
import crashContext from '../../../context/crashContext';
import UserContext from '../../../context/UserContext';
import axios from 'axios';
import { crashInstance } from '../../../config/axios';
import JoinUser from './JoinUser';

const Bets = () => {
  const [key, setKey] = useState('manual');
  const { userState } = useContext(crashContext);

  return (
    <div className='bets'>
      <div className='all-bets'>
        <Tabs
          id='controlled-tab-example'
          activeKey={key}
          onSelect={(k) => {
            if (!userState.dontToggle) {
              setKey(k);
            }
          }}>
          <Tab eventKey='manual' title='Manual'>
            <ManualBet />
          </Tab>
          <Tab eventKey='auto' title='Auto'>
            <AutoBet />
          </Tab>
        </Tabs>
      </div>
    </div>
  );
};

export default Bets;

const ManualBet = () => {
  const {
    userState,
    setUserState,
    handleUserChange,
    handleUserCalculation,
    gameState,
  } = useContext(crashContext);
  const { user, setUser } = useContext(UserContext);
  const [loader, setLoader] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');

  const makeBet = async () => {
    try {
      if (loader || gameState.gameRunning) {
        return;
      }

      if (!localStorage.getItem('token')) {
        // setErrorMessage('You need to login to make bet.');
        window.location.href = '/?login=login';
        return;
      }

      if (!userState.betAmount || userState.betAmount <= 0) {
        setErrorMessage('Bet coint cant be zero or less then it.');
        return;
      }

      if (userState.betAmount > user?.wallet) {
        setErrorMessage("You don't have enough coin to play");
        return;
      }
      setLoader(true);
      const crashResponse = await crashInstance().post('/joinCrash', {
        amount: userState.betAmount,
        autoCashoutAmount: userState.autoCashoutAmount,
      });
      setLoader(false);
      setUserState({
        ...userState,
        isUserBet: true,
        betAmount: 0,
        dontToggle: true,
      });
      console.log({ crashResponse });
    } catch (error) {
      setLoader(false);
      console.log(error);
      if (axios.isAxiosError(error) && error.response) {
        setErrorMessage(error.response.data.msg);
      }
    }
  };

  const dropBet = async () => {
    if (loader || !gameState.gameRunning) {
      return;
    }

    if (!localStorage.getItem('token')) {
      setErrorMessage('You need to login to make bet.');
      return;
    }

    try {
      setErrorMessage('');
      setLoader(true);
      const betResponse = await crashInstance().post('/crashDropBet');
      setLoader(false);
      setUserState({ ...userState, isBetDrop: true });
      const { profit } = betResponse.data;
      setUser({
        ...user,
        wallet: user?.wallet ? user?.wallet * profit : profit,
      });
    } catch (error) {
      setLoader(false);
      if (axios.isAxiosError(error) && error.response) {
        setErrorMessage(error.response.data.msg);
      }
    }
  };

  return (
    <div className='manual-bet bet-form'>
      <Form>
        <Form.Label>Bet Amount</Form.Label>
        <InputGroup>
          <Form.Control
            type='number'
            onChange={handleUserChange}
            placeholder='0.000000'
            name='betAmount'
            value={userState.betAmount}
          />
          <Button onClick={handleUserCalculation('betAmount', '1/2')}>
            1/2
          </Button>
          <Button onClick={handleUserCalculation('betAmount', '2x')}>2x</Button>
        </InputGroup>

        <Form.Label>Cashout at</Form.Label>
        <InputGroup>
          <Form.Control
            type='number'
            onChange={handleUserChange}
            placeholder='0.000000'
            name='autoCashoutAmount'
            value={userState.autoCashoutAmount}
          />
          <Button>X</Button>
        </InputGroup>
        {/* if game is running and user has make bet in the round so we need to
           show drop bet button
        */}
        {gameState.gameRunning && userState.isUserBet ? (
          <Button
            onClick={dropBet}
            // if user has drop his bet or he hits api so the loader is running
            // or if user has not make his bet in the round
            // so in that case we disable this button
            disabled={userState.isBetDrop || loader || !userState.isUserBet}>
            Drop bet
          </Button>
        ) : (
          <Button
            onClick={makeBet} className="btn-sbmit"
            disabled={gameState.gameRunning || loader || gameState.isGameLock}>
            {localStorage.getItem('token') ? 'Play' : 'Login'}
          </Button>
        )}

        {errorMessage}
      </Form>

      <JoinUser />
    </div>
  );
};

