import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { Toaster } from 'react-hot-toast';
import Crash from './components/games/crash';
import ResetPassword from './components/forget/resetPassword';
import UserContext from './context/UserContext';
import { getUser } from './utils/Api';
import logo from './assets/images/header/logo-txt.svg';
import logoicon from './assets/images/header/logo-img.svg';
import 'react-phone-input-2/lib/bootstrap.css';
import './assets/css/line-awesome.min.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Profile from './components/profile/profile';
import Settings from './components/profile/settings';
import BetHistory from './components/profile/bethistory';
import PrivacyPolicy from './components/legalPages/privacyPolicy';
import TermsConditions from './components/legalPages/termsconditions';
import FaqPage from './components/legalPages/FaqPage';
import UserRoute from './components/userRoutes/userRoutes';
import Wallet from './components/wallet/index';
import TransactionHistory from './components/profile/TransactionHistory';

function App() {
  const [user, setUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [tokens, setTokens] = useState(
    localStorage.getItem('token')
      ? JSON.parse(localStorage.getItem('token'))
      : {}
  );
  useEffect(() => {
    getUser(getUserCallback);
  }, []);

  const getUserCallback = (data) => {
    setIsLoading(false);
    if (data.success) {
      setUser(data.user);
    }
  };

  return (
    <UserContext.Provider value={{ user, setUser, tokens, setTokens }}>
      {isLoading ? (
        <Loader />
      ) : (
        <div className='App'>
          <Toaster />

          <Router>
            <Routes>
              <Route exact path='/' element={<Crash />} />
              <Route exact path='/' element={<UserRoute />}>
                <Route
                  exact
                  path='/reset-password'
                  element={<ResetPassword />}
                />
                <Route exact path='/wallet' element={<Wallet />} />
                <Route exact path='/profile' element={<Profile />} />
                <Route exact path='/settings' element={<Settings />} />
                <Route exact path='/betHistory' element={<BetHistory />} />
                <Route exact path='/transactionHistory' element={<TransactionHistory />} />
              </Route>
              <Route exact path='/privacy' element={<PrivacyPolicy />} />
              <Route exact path='/terms' element={<TermsConditions />} />
              <Route exact path='/faq' element={<FaqPage />} />
            </Routes>
          </Router>
        </div>
      )}
    </UserContext.Provider>
  );
}

export default App;

const Loader = () => {
  return (
    <div className='loader'>
      <div className='crash-logo'>
        <div className='animated-logo'>
          <img src={logo} alt='' />
          <img src={logoicon} alt='' className='animate-icon' />
        </div>
      </div>
    </div>
  );
};
