import React from "react";
import { Accordion } from "react-bootstrap";
import Layout from "../layout/layout";
import "./index.css";

const FaqPage = () => {
  const FaqArray = [
    {
      Header: "What is Lorem Ipsum?",
      Body: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.",
    },
    {
      Header: "Why do we use it?",
      Body: "It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for 'lorem ipsum' will uncover many web sites still in their infancy. Various versions have evolved over the years, sometimes by accident, sometimes on purpose (injected humour and the like)."
    },
    {
      Header: "Where can I get some?",
      Body: "There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by injected humour, or randomised words which don't look even slightly believable. If you are going to use a passage of Lorem Ipsum, you need to be sure there isn't anything embarrassing hidden in the middle of text. All the Lorem Ipsum generators on the Internet tend to repeat predefined chunks as necessary, making this the first true generator on the Internet. It uses a dictionary of over 200 Latin words, combined with a handful of model sentence structures, to generate Lorem Ipsum which looks reasonable. The generated Lorem Ipsum is therefore always free from repetition, injected humour, or non-characteristic words etc.",
    }
    ,
    {
      Header: "Where does it come from?",
      Body: "Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source.",
    },
    {
      Header: "Why is Lorem Ipsum Dolor used?",
      Body: "The Lorem Ipsum text is used to fill spaces designated to host texts that have not yet been published. They use programmers, graphic designers, typographers to get a real impression of the digital / advertising / editorial product they are working on."
    },
    {
      Header: "What is the original text of Lorem Ipsum Dolor Sit Amet?",
      Body: "The original Latin text From De finibus bonorum et malorum sections 1.10.32 and 1.10.33 - Marco Tullio Cicerone · « Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam eaque ipsa, quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt, explicabo. Nemo enim ipsam voluptatem, quia voluptas sit, aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos, qui ratione voluptatem sequi nesciunt, neque porro quisquam est, qui dolorem ipsum, quia dolor sit, amet, consectetur, adipisci velit, sed quia non",
    },
    {
      Header: "What are the origins of Lorem Ipsum Dolor Sit?",
      Body: "Its origins date back to 45 BC. In fact, his words were randomly extracted from the De finibus bonorum et malorum , a classic of Latin literature written by Cicero over 2000 years ago.",
    },
    {
      Header: "What is the meaning of Lorem ipsum?",
      Body: "Literally it does not mean anything. It is a sequence of words without a sense of Latin derivation that make up a text also known as filler text, fictitious, blind or placeholder."
    },
    {
      Header: "What is the most used version?",
      Body: "Lorem ipsum dolor sit amet, consectetur adipisci elit, sed eiusmod tempor incidunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrum exercitationem ullam corporis suscipit laboriosam, nisi ut aliquid ex ea commodi consequatur. Quis aute iure reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint obcaecat cupiditat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."
    }
  ];
  return (
    <Layout>
      <div className="legal-page">
        <div className="container">
          <div className="faq-section">
            <h1>Frequently Asked Questions</h1>
            <Accordion defaultActiveKey="0">
                {FaqArray.map((item,i)=>{
                    return (
                        <FaqItem itemHeader={item.Header} itemBody={item.Body} i={i}/>
                    )
                })}
            </Accordion>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default FaqPage;

const FaqItem = ({itemHeader, itemBody, i}) => {
    return (
        <Accordion.Item eventKey={i}>
                <Accordion.Header>{itemHeader}</Accordion.Header>
                <Accordion.Body>
                  <p>{itemBody}</p>
                </Accordion.Body>
              </Accordion.Item>
    )
}
