import Layout from '../../layout/layout';
import AllBets from './allbets';
import Bets from './bet';
import './crash.css';
import Game from './game';
import CrashContext from '../../../context/crashContext';
import { useState, useEffect, useRef } from 'react';
import socket from '../../../utils/socket';
import { crashInstance } from '../../../config/axios';

const Crash = () => {
  const [userState, setUserState] = useState({
    betAmount: 0,
    isUserBet: false,
    autoCashoutAmount: 0,
    betMode: 'manual',
    // If user makes bet in any tab so dont let user to bet
    // again from diffrent
    dontToggle: false,
    // We can make the drop bet button disable is this is true
    isBetDrop: false,
    stopProfit: 0,
    stopLoss: 0,
    winPercentage: 0,
    lossPercentage: 0,
    totalBets: 0,
  });

  const [gameState, setGameState] = useState({
    gameRunning: false,
    joinedUsers: [],
    // For clearing user list
    // and stopping user to bet when time reach to 1.2
    isGameLock: false,
  });
  console.log('GAME-STATE2', gameState);

  const handleUserChange = (e) => {
    const { name, value, type } = e.target;
    if (type === 'number' && value * 1 >= 0) {
      setUserState({ ...userState, [name]: value * 1 });
    } else if (type !== 'number') {
      setUserState({ ...userState, [name]: value });
    }
  };

  const handleUserCalculation = (field, calculation) => () => {
    let ans = userState[field];
    if (calculation === '1/2' && ans > 0) {
      ans = ans / 2 <= 1 ? 1 : ans / 2;
    } else if (calculation === '2x') {
      ans = ans <= 0 ? 1 : ans * 2;
    }
    setUserState({ ...userState, [field]: ans });
  };

  const canvasRef = useRef(null);
  // Because passing ref in the useEffect giving so many re renders
  // that why we move towards retry count
  // so the retry count will increase till the crash is not undefined
  const [retryCount, setRetryCount] = useState(0);

  useEffect(() => {
    (async () => {
      try {
        const currentUsers = await crashInstance().get('/getCurrentBeters');
        setGameState({
          ...gameState,
          joinedUsers: currentUsers.data.joinedPlayer,
        });
      } catch (error) {
        console.log(error);
      }
    })();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const crash = document.getElementById('crashGame').contentWindow;
    if (!crash) {
      setRetryCount(retryCount + 1);
      return;
    }

    // When game crashed we need to make the button visible
    const toggleGameRunning = (cur = false) => {
      // This will disable user from toggling the tab
      // disable the make bet button and show drop bet button
      // Don't set if value is same as before so if will not ganna do
      // any re render
      if (!cur && gameState.gameRunning) {
        setGameState({ ...gameState, gameRunning: cur });
      } else if (cur && !gameState.gameRunning) {
        setGameState({ ...gameState, gameRunning: cur });
      }
    };
    // To remove join player list and stop user to bet before starting the count down
    const resetGameState = (cur = false) => {
      if (cur && !gameState.isGameLock) {
        setGameState({ ...gameState, isGameLock: cur, gameRunning: cur });
      } else if (!cur && gameState.isGameLock) {
        setGameState({
          ...gameState,
          isGameLock: cur,
          joinedUsers: [],
          gameRunning: cur,
        });
      }
    };

    socket.on('CrashStartGame', () => {
      crash?.root?.showAssets();
      crash?.root?.introTxt('');
      crash?.root?.multiplierF(100);
      toggleGameRunning(true);
      resetGameState(true);
    });

    socket.on('CrashGameCrash', (data) => {
      crash?.root?.showAssets();
      crash?.root?.introTxt('');
      crash?.root?.multiplierF(data.curMultiplayer * 100);
      crash.root.stopRocket();
      toggleGameRunning(false);
    });

    socket.on('CrashMultiplayerValue', (data) => {
      // crash?.root?.introTxt('');
      crash?.root?.multiplierF(data.curMultiplayer * 100);
      toggleGameRunning(true);
      resetGameState(true);
    });

    socket.on('CrashGameRestarted', () => {
      crash?.root?.hideAssets();
      toggleGameRunning(false);
      setUserState({
        ...userState,
        isBetDrop: false,
        dontToggle: false,
        betAmount: 0,
        isUserBet: false,
        autoCashoutAmount: 0,
      });
    });

    socket.on('CrashRestartIn', (data) => {
      crash?.root?.hideAssets();
      crash?.root?.introTxt('Game start in ' + data?.gameStartIn / 1000);
      resetGameState(false);
    });

    return () => {
      socket.off('CrashStartGame');
      socket.off('CrashGameCrash');
      socket.off('CrashMultiplayerValue');
      socket.off('CrashGameRestarted');
      socket.off('CrashRestartIn');
    };
  }, [gameState, retryCount, userState]);

  // USER BETS SOCKETS
  useEffect(() => {
    socket.on('BetPlaceCrash', (data) => {
      // const { nickName, avatar, amount, userid, isCashedOut } = data;
      const prev = [...gameState.joinedUsers, data];
      setGameState({ ...gameState, joinedUsers: [...prev] });
    });

    socket.on('BetUpdate', (data) => {
      const { userid, amount } = data;
      const prev = [...gameState.joinedUsers];
      const findIndex = prev.findIndex((el) => el.userid._id === userid);
      if (findIndex !== -1) {
        prev[findIndex].amount += amount;
        setGameState({ ...gameState, joinedUsers: [...prev] });
      }
    });

    return () => {
      socket.off('BetPlaceCrash');
      socket.off('BetUpdate');
    };
  }, [gameState]);

  return (
    <CrashContext.Provider
      value={{
        userState,
        setUserState,
        gameState,
        setGameState,
        canvasRef,
        handleUserChange,
        handleUserCalculation,
      }}>
      <Layout>
        <div className='crash-game'>
          <div className='container'>
            <div className='crash-game-grid'>
              <Bets />
              <Game />
            </div>

            <AllBets />
          </div>
        </div>
      </Layout>
    </CrashContext.Provider>
  );
};

export default Crash;
