import { useState, useContext, useEffect } from 'react';
import { Button, Dropdown, DropdownButton, Modal } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import logo from '../../assets/images/header/logo-v2.png';
// import closeicon from '../../assets/images/chat/close.svg';
// import messageicon from '../../assets/images/chat/envelope.svg';
import usericon from '../../assets/images/chat/user1.png';
// import userIconImg from '../../assets/images/header/user-icon.svg';
// import logouticon from '../../assets/images/header/log-out-Regular.svg';
import Login from '../login/login';
import Register from '../register/register';
import Forget from '../forget/forget';
import VerifyEmail from '../login/verifyEmail';
import MailSentPopup from '../register/mailSentPopup';
import UserContext from '../../context/UserContext';
import toast from 'react-hot-toast';
import './header.css';
import { verifyEmail } from '../../utils/Api';
import { EnvelopeIconSVG, GearIconSVG, LogOutIconSVG, WalletIconSVG } from '../SVG/IconSVG';

const Header = ({ chatmenu, setChatMenu }) => {
  const urlParams = new URLSearchParams(window.location.search);
  const tokenFromParams = urlParams.get('token');
  const modalFromParams = urlParams.get('modal');
  const loginParams = urlParams.get('login');

  const checkVerifyModalShow =
    modalFromParams && tokenFromParams ? true : false;
  const { user } = useContext(UserContext);
  console.log({ user });
  const [show, setShow] = useState(false);
  const handleModal = (e) => {
    setShow(e);
  };

  useEffect(() => {
    console.log('loginParams', {
      loginParams,
      ans:
        !localStorage.getItem('token') &&
        (loginParams === 'login' || loginParams === 'register'),
    });
    if (
      !localStorage.getItem('token') &&
      (loginParams === 'login' || loginParams === 'register')
    ) {
      handleModal(loginParams);
    }
  }, [loginParams]);

  const handleLogout = () => {
    localStorage.removeItem('token');
    toast.success('Logout Successfully');
    window.location.reload(false);
  };
  const verifyUserByMail = async (token) => {
    if (tokenFromParams) {
      const res = await verifyEmail(token);
      if (res.code === 200) {
        setShow(modalFromParams);
      }
    }
  };
  useEffect(() => {
    if (checkVerifyModalShow) {
      verifyUserByMail(tokenFromParams);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="header">
      <div className="container">
          
        <div className="header-grid">
          <Link to="/" className="logo">
            <img src={logo} alt="" />
          </Link>
          <div className="header-menu-section">
            {user ? (
              <DropdownButton
                className='user-loggedin'
                id='dropdown-item-button'
                title={
                  <>
                    <span>{user.nickName}</span>
                    <img src={user?.avatar ? user.avatar : usericon} alt={user.name} />
                  </>
                }>
                <Dropdown.ItemText>
                  <img src={user?.avatar ? user.avatar : usericon} alt={user.name} />
                  {user.name}
                  </Dropdown.ItemText>
                <Dropdown.Item href='/wallet'>
                  <WalletIconSVG />
                   Wallet
                </Dropdown.Item>
                <Dropdown.Item href='/profile'>
                  <GearIconSVG />
                  {/* <img src={userIconImg} alt='icon' /> */}
                   Profile
                </Dropdown.Item>
                <Dropdown.Item as='button' onClick={handleLogout}>
                  <LogOutIconSVG />
                  {/* <img src={logouticon} alt='logout' /> */}
                   Logout
                </Dropdown.Item>
              </DropdownButton>
            ) : (
              <div className="auth-box">
                <Button onClick={() => handleModal("login")}>Login</Button>
                <Button onClick={() => handleModal("register")}>Sign Up</Button>
              </div>
            )}

            {/* <div className="chat-mbl">
              <span
                onClick={() => setChatMenu(!chatmenu)}
                className={`${chatmenu ? `` : ``} `}
                role="presentation"
              >
                {chatmenu ? (
                  <img src={messageicon} alt="" />
                ) : (
                  <img src={closeicon} alt="" />
                )}
              </span>
            </div> */}
            {!chatmenu &&
              <div className="chat-mbl" onClick={() => setChatMenu(!chatmenu)}>
                {/* <img src={messageicon} alt="" /> */}
                <EnvelopeIconSVG />
            </div>
            }

          </div>
        </div>
      </div>

      <Modal
        centered
        className="login-popup"
        show={show === "login"}
        onHide={() => handleModal(false)}
      >
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Login handleModal={handleModal} />
        </Modal.Body>
      </Modal>

      <Modal
        centered
        className="signup-popup"
        show={show === "register"}
        onHide={() => handleModal(false)}
        setShow={setShow}
      >
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Register handleModal={handleModal} />
        </Modal.Body>
      </Modal>
      <Modal
        centered
        className="login-popup"
        show={show === "forget"}
        onHide={() => handleModal(false)}
      >
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Forget handleModal={handleModal} />
        </Modal.Body>
      </Modal>
      <Modal
        centered
        className="login-popup"
        show={show === "verifying"}
        onHide={() => handleModal(false)}
      >
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <MailSentPopup
            handleModal={handleModal}
            text={
              checkVerifyModalShow
                ? "Your account has been verified"
                : "Verification link has been send to your mail"
            }
            checkModalType={checkVerifyModalShow}
          />
        </Modal.Body>
      </Modal>
      <Modal
        centered
        className="login-popup"
        show={show === "verify-email"}
        onHide={() => handleModal(false)}
      >
        <Modal.Header closeButton>
          <Modal.Title></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <VerifyEmail handleModal={handleModal} />
        </Modal.Body>
      </Modal>
    </div>
  );
};

export default Header;
