import { useState, useEffect, useContext } from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { useNavigate } from "react-router-dom";
import { LoginSchema } from "../../validationSchema/authSchema";
import { login } from "../../utils/Api";
import UserContext from "../../context/UserContext";
import { Form, Button, Spinner } from "react-bootstrap";
import toast from "react-hot-toast";
import "./login.css";

const Login = ({ handleModal }) => {
  const navigate = useNavigate();
  const { setUser, user } = useContext(UserContext);
  const [isLoading, setIsLoading] = useState(false);
  // const [pass, setPass] = useState("password");
  // const handlePass = () => {
  //   setPass(!pass);
  // };
  useEffect(() => {
    if (user) {
      navigate("/");
    }
  }, [user, navigate]);
  const {
    handleSubmit,
    register,
    setError,
    formState: { errors },
  } = useForm({ resolver: yupResolver(LoginSchema) });

  const loginUser = async (values) => {
    setIsLoading(true);
    const res = await login(values);
    setIsLoading(false);
    if (res.message === "Incorrect email") {
      setError(
        "email",
        { type: "custom", message: "Invalid email" },
        { shouldFocus: true }
      );
    } else if (res.message === "Incorrect password") {
      setError(
        "password",
        { type: "custom", message: "Invalid password" },
        { shouldFocus: true }
      );
    } else if (res.message === "Please verify your email") {
      handleModal("verify-email");
    } else if (res.message === "Not found") {
      setError(
        "password",
        { type: "custom", message: "Invalid request" },
        { shouldFocus: true }
      );
    } else if (res.user) {
      localStorage.setItem("token", JSON.stringify(res.tokens));
      // setTokens(res.tokens);
      setUser(res.user);
      toast.success("Login Successfully");
      handleModal(false);
      navigate("/");
    }
    //   console.log("Res =>", res);
  };

  return (
    <div className='login'>
      <div className='login-section'>
        <div className='container'>
          <div className='row'>
            <div className='col-md-12'>
              <div className='login-form'>
                <h1>Log In</h1>
                <p>Welcome, Please login to your account</p>
                <div className='login-box'>
                  <Form onSubmit={handleSubmit(loginUser)}>
                    <Form.Group className='form-group'>
                      <Form.Label>Username / Email</Form.Label>
                      <Form.Control
                        type='text'
                        placeholder='Enter your username or email'
                        name='email'
                        className={errors.email ? "error-field" : ""}
                        {...register("email")}
                        autoComplete='off'
                      />
                      {errors?.email ? (
                        <p className='error-text'>{errors.email.message}</p>
                      ) : (
                        ""
                      )}
                    </Form.Group>
                    <Form.Group className='form-group'>
                      <Form.Label>Password</Form.Label>
                      <Form.Control
                        type='password'
                        placeholder='Enter your password'
                        name='password'
                        className={errors.password ? "error-field" : ""}
                        {...register("password")}
                        autoComplete='off'
                      />
                      {/* <span className='' onClick={handlePass}>
                        <i class={pass ? "las la-eye" : "las la-eye-slash"}></i>
                      </span> */}
                      {errors?.password ? (
                        <p className='error-text'>{errors.password.message}</p>
                      ) : (
                        ""
                      )}
                    </Form.Group>
                    <div className='forget-password'>
                      {/* <div className="remember-me">
                                                <Form.Check type="checkbox" label="Remember me" />
                                            </div> */}
                      <Button
                        className='forget-link'
                        onClick={() => handleModal("forget")}>
                        Forget Password?
                      </Button>
                    </div>
                    <div className='login-button'>
                      <Button type='submit' className='l-btn'>
                        {isLoading ? <Spinner animation='border' /> : "Login"}
                      </Button>
                    </div>
                    {/* <div className="social-login">
                                            <span>OR</span>
                                            <div className="social-btns">
                                                <Button className="facebook-btn"><i className="fa fa-facebook" /> Login with Facebook</Button>
                                                <Button className="twitch-btn"><i className="fa fa-steam" /> Login with Steam</Button>
                                            </div>
                                        </div> */}
                  </Form>
                  <p className='already'>
                    Don't have an account yet?{" "}
                    <Button
                      className='forget-link'
                      onClick={() => handleModal("register")}>
                      Register
                    </Button>{" "}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
