import React, { useState, useRef, useContext, useEffect, useCallback } from "react";
import { Button, FormControl, Form } from "react-bootstrap";
import emoji from "../../assets/images/chat/emoji.svg";
// import sendicon from "../../assets/images/chat/send.svg";
import "./chat.css";
import socket from "../../utils/socket";
import usericon from '../../assets/images/chat/user1.png';
import axios from "axios";
import UserContext from "../../context/UserContext";
import toast from "react-hot-toast";
import { server } from "../../config/keys";
import { getToken } from "../../config/axios";
import { SendIconSVG } from "../SVG/IconSVG";


const GlobalChat = () => {
    const messagesEndRef = useRef(null);
    const { user } = useContext(UserContext);
    const [chatMessage, setChatmessage] = useState();
    const [allChatData, setAllChatData] = useState([]);
    const [dummy, setDummy] = useState(false);
    const [spamBlock, setSpamBlock] = useState(false);
    useEffect(() => {
      let isMounted = true;
      (async () => {
        try {
          const alldata = await axios.get(`${server}/v1/users/get-global-chat`);
          if (isMounted && alldata?.data) {
            setAllChatData(alldata?.data);
          }
        } catch (error) {
          console.log(error);
        }
      })();
  
      return () => {
        isMounted = false;
      };
    }, [dummy]);
  
    const scrollToBottom = useCallback(() => {
      messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
    }, [messagesEndRef]);
    useEffect(() => {
      socket.on("newChatMessage", (data) => {
        setAllChatData((prev) => [...prev, data]);
      });
  
      socket.on("spammer", () => {
        setSpamBlock(true);
        toast.error(
          "You are spamming in chat. So we have blocked you for a minute.",
          {
            duration: 2000,
            positionm: "center",
          }
        );
      });
  
      socket.on("spam-unblock", () => {
        setSpamBlock(false);
      });
  
      return () => {
        socket.off("newChatMessage");
        socket.off("spammer");
        socket.off("spam-unblock");
      };
    }, []);
  
    const handleChat = (e) => {
      setChatmessage(e.target.value);
    };
    const handleChatSubmit = (e) => {
      e.preventDefault();
      if (e.key === "Enter") {
        return ;
      }
      if (!getToken()) {
        toast.error("You are not logged in . Please login to send message.");
      }
      socket.emit("chatMessageByUser", {
        chatMessage,
        userToken: getToken(),
        name: user?.name ?? "",
        //   avatar: user?.avatar ?? "",
      });
      setDummy(true);
      setChatmessage("");
    };
  
    useEffect(() => {
        scrollToBottom();
      // console.log("hhhhhhhhh")
    }, [messagesEndRef,allChatData,scrollToBottom]);
  
    // useEffect(() => {
    //   const handleChatEvent = (e) => {
    //     if (e.key === "Enter") {
    //       if (!getToken()) {
    //         toast.error("You are not logged in . Please login to send message.", {
    //           duration: 2000,
    //           positionm: "center",
    //         });
    //       }
    //       socket.emit("chatMessageByUser", {
    //         chatMessage,
    //         userToken: getToken(),
    //         username: user?.username ?? "",
    //         avatar: user?.avatar ?? "",
    //       });
    //       scrollToBottom();
    //       setDummy(true);
    //       setChatmessage("");
    //     }
    //   };
    //   window.addEventListener("keydown", handleChatEvent);
    //   return () => {
    //     window.removeEventListener("keydown", handleChatEvent);
    //   };
    // }, [chatMessage, user?.avatar, user?.username]);
    //   Global Chat End------
    return (
      <div className="chat-content global-chat">
        {/* {chatmessage ? <div className="chat-paused">The chat is paused while you scrolling</div> : ""} */}
        <div
          className="chat-box"
          // onScroll={handleMsg} ref={msgRef}
        >
       
          {allChatData &&
            allChatData?.length > 0 &&
            allChatData?.map((item, i) => {
              return (
                <div key={i}>
               
                  <div className="container1">
                    <div className="chat-user">
                      <div className="chat-user-info">
                        <img src={item?.userId?.avatar ? item?.userId?.avatar :usericon} alt="" />
                        <h4>
                          {item?.userId?.name}:{" "}
                          {/* <span className="nickname">@sreb@123</span> */}
                          <span ref={messagesEndRef}>{item?.message}</span>
                        </h4>
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
        </div>
  
        <div className="chat-footer">
          <Form>
            <div className="chat-input">
              <FormControl
                disabled={spamBlock}
                placeholder="Your Message"
                maxLength={160}
                value={chatMessage}
                onChange={(e) => handleChat(e)}
              />
              <img src={emoji} alt="" />
            </div>
            <Button type="submit" onClick={handleChatSubmit}>
              <SendIconSVG />
              {/* <img src={sendicon} alt="" /> */}
            </Button>
          </Form>
          {/* <h2>192 Online <span>{character}/160</span></h2> */}
        </div>
      </div>
    );
  };

  export default GlobalChat;