import crashContext from '../../../context/crashContext';

import { useContext } from 'react';

const Game = () => {
  const { canvasRef } = useContext(crashContext);

  return (
    <>
      <div className='crash-game-view'>
        <iframe
          ref={canvasRef}
          src='../../../indexCrash.html'
          title='crash'
          id='crashGame'></iframe>
      </div>
    </>
  );
};

export default Game;
