import React, {useContext, useState } from "react";
import { Form, Button } from "react-bootstrap";
import ProfileLayout from "./profileLayout";
import { updateUserPassword} from "../../utils/Api";
import UserContext from '../../context/UserContext';
import toast from "react-hot-toast";
const Settings = () => {
  const initial ={
    currentPassword:'',
    newPassword:'',
    confirmPassword:''
  }
  const {  user,setUser } = useContext(UserContext);
  const [showPassword, setShowPassword] = useState(false);
  const [showNewPassword, setShowNewPassword] = useState(false);
  const [userData, setUserData] = useState(initial);
  const [errors, setErrors] = useState({});
  console.log(setShowPassword, setShowNewPassword);

	
	const handleOnChange = (e) => {
		const { name, value } = e.target;
		setUserData({ ...userData, [name]: value });
	  };
    // let checkPassword =
    // /^(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*#?&])[A-Za-z\d@$!%*#?&]{8,}$/;

    const handleOnblur = (e) => {
      const {name,value}=e.target; 
     if (value === '') {
        setErrors({[name]: `This field required` });
      }
      else if (
        name === 'confirmPassword' &&
        userData.newPassword !== userData.confirmPassword
      )
      {  
        setErrors({[name]: `Password does not match` });
      }

    };
   
    // const handleKeyUp = (e) => {
    //   const { name, value } = e.target;
    //   if (value !== '') {
    //     setErrors({ ...errors, [name]: '' });
    //   }
    // };

 const validate =(value) =>{
  let errors = {};
  let isValid = true ;
  if (value.currentPassword === '')
  {
    errors.currentPassword ="current password required";
    isValid = false;
  }
   if(value.newPassword === '')
  {
    errors.newPassword ="new password required";
    isValid = false;
  }
  if(value.confirmPassword === ''){
    errors.confirmPassword ="confirm password required";
    isValid = false;
  }
return { isValid,errors}
 }
  const handleUpdateUser = async (e) => {
    e.preventDefault();
    try{
    const { currentPassword,newPassword} =userData;
    const validation  = validate(userData);
      setErrors(validation.errors);
      
      
    
      if(Object.keys(validation.errors).length !== 0)
      {
        return 
      }
  
    const payload ={ currentPassword,newPassword,id:user.id}
    const response = await updateUserPassword(payload);
    const {status,data}=response || {};
   
		if(status===201){
			toast.success('Password update successfully',{toastId:"update passowrd"});
			setUser(data)
				setUserData(initial)
		  }

    }catch (error) {
      
      let er = {};
      error.inner.forEach((e) => {
        er[e.path] = e.message;
      });
      setErrors({ ...er });
    }
 
  };


  
  return (
    <ProfileLayout>
      <div className="settings-page ">
        <div className="page-heading">
          <h3>Update Password</h3>
        </div>
{console.log("validation",errors)}
        <div className="settings-content ">
          <Form onSubmit={handleUpdateUser}>
            <div className="mb-3 form-group">
              <Form.Label>Current Password</Form.Label>
              <div className="form-input">
                <Form.Control
                  type={showPassword ? "text" : "password"}
                  autocomplete="off"
                  name="currentPassword"
                  value={userData.currentPassword || ''}
                  onChange={handleOnChange}
                  onBlur={handleOnblur}
                  // onKeyUp={handleKeyUp}
                />
                <i className="fa fa-eye text-white"></i>
                {errors && errors.currentPassword && (
                        <span className='text-danger'>{errors.currentPassword}</span>
                      )}
              </div>
            </div>
            <div className="mb-3 form-group">
              <Form.Label>New Password</Form.Label>
              <div className="form-input">
                <Form.Control
                  type={showNewPassword ? "text" : "password"}
                  autocomplete="off"
                  name="newPassword"
                  value={userData.newPassword }
                  onChange={handleOnChange}
                  onBlur={handleOnblur}
                  // onKeyUp={handleKeyUp}
                />
                <i className="fa fa-eye text-white"></i>
                {errors && errors?.newPassword && (
                        <span className='text-danger'>{errors.newPassword}</span>
                      )}
              </div>
            </div>
            <div className="mb-3 form-group">
            <Form.Label>Confirm Password</Form.Label>
            <div className="form-input">
              <Form.Control
                type="password"
                autoComplete="confirmpassword"
                name="confirmPassword"
                value={userData.confirmPassword }
                onChange={handleOnChange}
                onBlur={handleOnblur}
                // onKeyUp={handleKeyUp}
              />
            </div>
            {errors && errors?.confirmPassword && (
                        <span className='text-danger'>{errors.confirmPassword}</span>
                      )}
            </div>
            
            <div className="settings-btn">
              <Button className="save-btn" type="submit">
                Update
              </Button>
            </div>
          </Form>
        </div>
      </div>
    </ProfileLayout>
  );
};

export default Settings;
